@use './../../assets/sass/vars' as *;
@use './sass/style-overlay-image';
@use './sass/style-overlay-image-compact';
@use './sass/style-soft-background';
@use './sass/style-soft-background-compact';
@use './sass/style-solid-background';
@use './sass/style-solid-background-compact';


.wp-block-dahlia-blocks-category-grid {
    .category-grid {
        &.has-blur{
            &:before{
                backdrop-filter: blur(4px);
                
            }
            
        }
        &__outer {
           
        }

        &__container {
     
            display: grid;
            gap: var(--grid-gap-sm, 1rem);
            grid-template-columns: repeat(var(--grid-columns-mobile), 1fr);
            grid-auto-rows: 1fr;
            @media(min-width: $screen-sm) {

                gap: var(--grid-gap-md, 1.2rem);
                grid-template-columns: repeat(var(--grid-columns-tablet), 1fr);

            }

            @media(min-width: $screen-md) {

                gap: var(--grid-gap-md, 1.2rem);
                grid-template-columns: repeat(var(--grid-columns-large-tablet), 1fr);

            }
            @media(min-width: $screen-lg) {

                gap: var(--grid-gap-md, 1.2rem);
                grid-template-columns: repeat(var(--grid-columns-large-tablet), 1fr);

            }

            @media(min-width: $screen-xl) {

                gap: var(--grid-gap-lg, 1.2rem);
                grid-template-columns: repeat(var(--grid-columns-desktop), 1fr);

            }

            @media(min-width: $screen-xxl) {

                gap: var(--grid-gap-xl, 1.2rem);
                grid-template-columns: repeat(var(--grid-columns-large), 1fr);

            }
        }


    }

    .category-card {

        &__outer {
            height:100%;
        }

        &__container {
            height:100%;
            .card-icon {
                &__outer {
                   border-radius:50%;
                    aspect-ratio: 1 / 1;
                    flex-shrink: 0; // evitem deformaci√≥ dins contenidors flexibles o grid

                }

                &__container {
                    .dahlia-icon{
                
                        display: block;
                    }
                }
            }
        }


    }

}

          